.icon-tianjia:before { content: "\E613"; }

.icon-aixin:before { content: "\E663"; }

.icon-shanchu:before { content: "\E635"; }

* {

   -webkit-box-sizing: border-box;

    box-sizing: border-box;

}

/*
 * 禁止文本缩放 字体设置  取消touch高亮效果
 */

html {

    width: 100%;

    height: 100%;

   margin: 0px;

   padding: 0px;

   -webkit-text-size-adjust: 100%;

   -webkit-tap-highlight-color: rgba(0, 0, 0, 0);

   font-size: 12px;

    background: transparent;

   }

body {

    width: 100%;

    height: 100%;
    
    font-family: -apple-system, "PingFang SC","Helvetica Neue", Helvetica, STHeiTi, sans-serif,"Segoe UI","Lucida Grande",Arial,"Microsoft YaHei",FreeSans,Arimo,"Droid Sans","wenquanyi micro hei","Hiragino Sans GB","Hiragino Sans GB W3",FontAwesome;

   -webkit-overflow-scrolling: touch;
   
   overflow-x: hidden;
   

 }

body, div, dl, dt, dd, ul, ol, li, h1, h2, h3, h4, h5, h6, pre, code, form, fieldset, legend, input, textarea, p, blockquote, th, td, hr, button, article, aside, details, figcaption, figure, footer, header, hgroup, menu, nav, section, sumary {

    margin: 0;

    padding: 0;

}

/*
 * 清除输入框内阴影
 */

input, select,textarea {

border: none;

-moz-appearance:none;

     appearance:none;

-webkit-appearance: none;

outline: none;

resize: none;

-webkit-box-sizing: border-box;

        box-sizing: border-box;

border-radius: 0;

}

input[type=number] {  
    -moz-appearance:textfield;  
}

input[type=number]::-webkit-inner-spin-button,  
input[type=number]::-webkit-outer-spin-button {  
    -webkit-appearance: none;  
    margin: 0;  
}

ol, ul {

    list-style: none;

}

/*
 * 禁止选中文本内容
 */

/**:not(input, select, textArea) {

    -webkit-user-select: none;

}*/

/*
 * 禁用长按页面时的弹出菜单(iOS下有效) ,img和a标签都要加
 */

img,a{

-webkit-touch-callout:none;

}

a,a:active,a:hover {

text-decoration: none;

}

/*
 * 去掉点击链接和文本框对象时默认的灰色半透明覆盖层(iOS)或者虚框(Android)
 */

a,button,input,textarea{ 

-webkit-tap-highlight-color:rgba(0,0,0,0);

}

.noselect {

-webkit-touch-callout: none; /* iOS Safari */

-webkit-user-select: none; /* Chrome/Safari/Opera */ /* Konqueror */

-moz-user-select: none; /* Firefox */

-ms-user-select: none; /* Internet Explorer/Edge */

user-select: none; /* Non-prefixed version, currently

not supported by any browser */

}

.clearFix:after{
  clear: both;
  display: block;
  content: '';
}

.left-control{
  width: 300px;
  height: 100%;
  background: #F5F5F5;
  position: relative;
  -webkit-box-shadow: 0 0 10px rgba(153, 148, 148, 0.5);
          box-shadow: 0 0 10px rgba(153, 148, 148, 0.5);
  padding-top: 30px;
  float: left;
  padding-bottom: 40px;
  overflow: hidden;
}

.left-control>.title{
  width: 100%;
  height: 30px;
  line-height: 30px;
  font-size: 22px;
  text-align: center;
  color: #939191;
}

.left-control .choose_btns{
    overflow: hidden;
    width: 100%;
    height: 40px;
    line-height: 40px;
    margin-top: 20px;
}

.left-control .choose_btns li{
  float: left;
  width: 50%;
  text-align: center;
  height: 40px;
  background: #dbdbdb;
  font-size: 16px;
  cursor: pointer;
  color: #7c7c7c;
}

.left-control .choose_btns li.active{
      background: #3fbff6;
    color: #fff;
    font-weight: 700;
}

.left-control .add{
      position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 40px;
    line-height: 40px;
    font-size: 18px;
    text-align: center;
    background: #dbdbdb;
    color: #6a6666;
    cursor: pointer;
    -webkit-box-shadow: 0 1px 10px rgba(37, 35, 35, 0.5);
            box-shadow: 0 1px 10px rgba(37, 35, 35, 0.5);
    -webkit-transition: 0.5s;
    transition: 0.5s;
}

.left-control .add:hover{
  background: #ccc;
}

.notesList{
  width: 100%;
  overflow: scroll;
  height: calc(100% - 90px);
}

.notesList li{
      width: calc(100% - 60px);
    line-height: 20px;
    font-size: 14px;
    background: #dbdbdb;
    color: #7c7c7c;
    cursor: pointer;
    border-top: 1px solid #c9c9c9;
    position: relative;
}

.notesList li.active{
  background: #3fbff6;
    color: #fff;
}

.notesList li .content_box{
  float: left;
}

.notesList li .partial_content{
      float: left;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
    width: 230px;
    overflow: hidden;
    margin: 10px 0 10px 10px;
        word-wrap: break-word;
    word-break: break-all;
}

.notesList li .option{
  position: absolute;
  right: -60px;
  top: -1px;
  width: 60px;
  height: 102%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  font-size: 18px;
      background: #dbdbdb;
      float: left;
      border-top: 1px solid #c9c9c9;
}

.notesList li.active .option{
      background: #3fbff6;
}

.notesList li .love,.notesList li .del{
  color: #fff;
      width: 25px;
    text-align: center;
    float: left;
    cursor: pointer;
}

.notesList li .love.active{
  color: #f96c6c;
}

.notesList li .del{
  color: #a4a4a4;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}

.notesList li.active .del{
  color: #fff;
}

.notesList li .del:hover{
  color: #898888;
}

textarea{
  width: calc(100% - 300px);
  height: 100%;
  display: block;
  padding:10px 20px;
  font-size: 16px;
  color: #5b5858;
      word-wrap: break-word;
    word-break: break-all;
}